export const DateService = {
    getDateString: function (inpValue, inpFormat) {
        if ( inpValue ) {
            // TO DO: MORE VALIDATIONS AND PARSING
            return inpValue.format("DD-MMM-YYYY");
        } else if( inpFormat && inpValue ) {
            // TO DO: MORE VALIDATIONS AND PARSING
            return inpValue.format(inpFormat);
        } else {
            let now = new Date(),
            dd = now.getDate(),
            mm = now.getMonth(),
            yyyy = now.getFullYear();
            const monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July',
                'August', 'September', 'October', 'November', 'December'];
            return dd + '-' + monthNames[mm] + '-' + yyyy;
        }
    },
    isInRange: function ( inpStartDate, inpEndDate ) {
        return true;
    }
};


